<template>
  <div class="loginPage">
    <!--  -->
    <header-default :title="''"></header-default>
    <!--  -->
    <div class="main">
      <!--  -->
      <van-cell-group>
        <!--  -->
        <van-field
          v-model="username"
          required
          clearable
          label=""
          placeholder=""
          @click-right-icon="$toast('question')"
        />
        <!--  -->
        <van-field
          v-model="password"
          type="password"
          label=""
          placeholder=""
          required
        />
        <!--  -->
        <van-field
          v-model="checking"
          center
          required
          clearable
          label=""
          placeholder=""
          :right-icon="imgsrc"
          @click-right-icon="refresh"
        >
        </van-field>
      </van-cell-group>
      <!--  -->
      <p class="tips">: , </p>
      <p class="tips"></p>
      <!--  -->
      <div class="login_btn">
        <van-button size="large" type="primary" @click="login"></van-button>
      </div>
      <!--  -->
      <Notice :title="lack_word" :show="show" @hidden-notice="hiddenNotice"></Notice>
      <!--  -->
      <div class="repassword">
        <router-link to="/home" class="word">?</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import headerDefault from '../components/header-default'
import Notice from '../components/Notice'
export default {
  data () {
    return {
      // 
      username: '',
      // 
      password: '',
      // 
      checking: '',
      // 
      imgsrc: '',
      // , 
      show: false,
      // ()
      lack_word: '',
      // ( lack_word )
      lack_username: '//',
      lack_pw: '',
      lack_check: ''
    }
  },
  created () {
    this.getCaptchas()
  },
  methods: {
    // 
    getCaptchas () {
      this.$api.captchas()
      .then(res => {
        this.imgsrc = res.data.code
      })
      .catch(err => {
        this.$api.error(err)
      })
    },
    // 
    refresh () {
      this.getCaptchas()
    },
    // 
    login () {
      // 
      var flag = this.lack_keyword()
      if (!flag) return
    },
    // 
    lack_keyword () {
      if (!this.username) {
        this.lack_word = this.lack_username
      } else if (!this.password) {
        this.lack_word = this.lack_pw
      } else if (!this.checking) {
        this.lack_word = this.lack_check
      } else {
        return true
      }
      this.show = true
    },
    // ( show(), )
    hiddenNotice () {
      this.show = false
    }
  },
  components: {
    headerDefault,
    Notice
  }
}
</script>

<style lang="scss">
  .loginPage {
    @import '../static/styles/mixin.scss';
    .main {
      padding-top: 12px;
      // 
      .van-field__right-icon {
        width: 100px;
        height: 30px;
        // 
        .van-icon--image {
          width: 100%;
          height: 100%;
        }
      }
      // 
      .tips {
        padding: 10px;
        font-size: 12px;
        color: red;
      }
      // 
      .login_btn {
        padding: 0 13px;
      }
      // 
      .repassword {
        padding: 25px 13px;
        text-align: right;
        .word {
          font-size: 14px;
          color: $maincl;
        }
      }
    }
  }
</style>
