<template>
  <div class="cityPage">
    <Header class="HeaderCPNT">
      <template v-slot:left>
        <van-icon name="arrow-left" @click="back" class="back" />
      </template>
      <template v-slot:default>
        <span class="city_name">{{ city_info.name }}</span>
      </template>
      <template v-slot:right>
        <span @click="changeCity"></span>
      </template>
    </Header>
    <main class="main">
      <div class="search">
        <form>
          <input type="search" class="input_search" v-model="keyword" required placeholder="">
          <input type="submit" class="search_btn" @click="showSearch">
        </form>
      </div>
      <div class="search_res">
        <!--  -->
        <p class="word" v-if="search_flag === 0"></p>
        <!--  -->
        <div class="no_res" v-if="search_flag === 2">! </div>
        <!-- ,  res  -->
        <div class="show_search">
          <div class="site_box" v-for="item of res" :key="item.geohash" @click="goandsave(item)">
            <p class="site_name">{{ item.name }}</p>
            <p class="site_addr">{{ item.address }}</p>
          </div>
        </div>
        <!--   -->
        <div class="clear_all" @click="clearAll" v-if="search_flag === 0 && search_his.length !== 0"></div>
      </div>
    </main>
  </div>
</template>

<script>
import Header from '../components/header'
export default {
  props: ['id'],
  data () {
    return {
      // , 0 , 1 , 2 
      search_flag: 0,
      // 
      keyword: '',
      // ()
      city_info: {},
      // , res
      search_his: [],
      // 
      res: [] // ,  no_res
    }
  },
  created () {
    this.getCityInfo()
    var temp_his = window.localStorage.getItem('addr_his')
    this.search_his = temp_his ? JSON.parse(temp_his) : []
    this.res = this.search_his
  },
  methods: {
    // 
    back () {
      this.$router.push({path: '/home'})
    },
    // ()
    getCityInfo () {
      this.$api.cityInfo(this.id)
      .then(res => {
        this.city_info = res.data
      })
      .catch(err => {
        this.$api.error(err)
      })
    },
    // 
    showSearch (e) {
      // 
      if (this.keyword === '') return
      // 
      e.preventDefault()
      // 
      this.getSearchInfo()
    },
    // 
    getSearchInfo () {
      this.$api.searchInfo({
        city_id: this.id,
        keyword: this.keyword
      })
      .then(res => {
        this.res = res.data
        // 
        this.isExist()
      })
      .catch(err => {
        this.$app.error(err)
      })
    },
    // 
    isExist () {
      if (this.res.length === 0) {
        this.search_flag = 2
        return
      }
      this.search_flag = 1
    },
    // 
    goandsave (item) {
      // 
      var flag = this.search_his.some(res => {
        // 
        return res.geohash === item.geohash
      })
      if (!flag) {
        this.search_his.unshift(item)
        window.localStorage.setItem('addr_his', JSON.stringify(this.search_his))
      }
      // 
      this.$store.commit('getSiteInfo', item)
      // 
      this.$router.push({path: '/msite', query: { geohash: item.geohash }})
    },
    // 
    clearAll () {
      window.localStorage.removeItem('addr_his')
      // 
      this.search_his = this.res = []
    },
    // 
    back () {
      this.$router.go(-1)
    },
    // 
    changeCity () {
      this.$router.push({path: '/home'})
    }
  },
  watch: {
    keyword (val) {
      if (val !== '') return
      this.search_flag = 0
      this.res = this.search_his
    }
  },
  components: {
    Header
  }
}
</script>

<style lang="scss" scope>
  .cityPage {
    @import '../static/styles/mixin';
    width: 375px;
    .HeaderCPNT {
      .back {
        height: 48px;
        line-height: 48px;
        font-size: 24px;
      }
      .city_name {
        overflow:hidden;
        font-size: 20px;
        font-weight: bold;
      }
    }
    .main {
      .search {
        @include bgc(white);
        margin-top: 12px;
        padding-bottom: 10px;
        padding-top: 15px;
        border-bottom: 2px solid $bordercl;
        .input_search {
          display: block;
          width: 340px;
          height: 36px;
          border: 1px solid #ccc;
          border-radius: 2px;
          margin: 0 auto;
          margin-bottom: 12px;
          padding-left: 10px;
        }
        .search_btn {
          @include bgc($maincl);
          display: block;
          border: none;
          color: #fff;
          width: 340px;
          height: 36px;
          margin: 0 auto;
          border-radius: 4px;
        }
      }
      .search_res {
        .word {
          font-size: 13px;
          padding: 2px 10px;
          border-bottom: 1px solid $bordercl;
        }
        .no_res {
          @include bgc(white);
          padding: 10px;
        }
        .show_search {
          .site_box {
            @include bgc(white);
            padding: 12px 18px;
            height: 75px;
            border-bottom: 1px solid $bordercl;
            .site_name {
              font-size: 17px;
              padding-bottom: 8px;
            }
            .site_addr {
              overflow: hidden;
              font-size: 13px;
              color: #aaa;
            }
          }
        }
        .clear_all {
          color: #555;
          font-size: 18px;
          padding: 15px;
          text-align: center;
          @include bgc(white);
          border-bottom: 1px solid $bordercl;
        }
      }
    }
  }
</style>
