import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    site: {},
    explain: {},
    explainCaption: [],
    explainContent: [],
    parentScroll: undefined
  },
  mutations: {
    //  City 
    getSiteInfo (state, site) {
      state.site = site
    },
    //  getSiteInfo 
    getInfo (state, info) {
      state[info.name] = info.data
    }
  },
  actions: {

  }
})
