<template>
  <div class="searchPage">
    <!--  -->
    <header-default :title="''"></header-default>
    <!--  -->
    <div class="keyword">
      <form action="">
        <input type="search" class="search_input" v-model="keyword" placeholder="">
        <input type="submit" class="search_btn" @click="showRes">
      </form>
    </div>
    <!--  -->
    <div class="main">
      <!--  -->
      <!-- ,  isHistory ,  -->
      <div class="history" v-if="isHistory()">
        <p class="word"></p>
        <div class="content">
          <p class="search_history_word" @click="his_showRes(item)" v-for="item of his" :key="item">{{ item }}</p>
          <div class="clearAll" @click="clearAll"></div>
        </div>
      </div>
      <!--  -->
      <div class="res" v-if="res_flag == 1">
        <p class="shopWord"></p>
        <div class="shopCard">
          <shopCard-search v-for="item of res" :key="item.id" :geohash="geohash" :res="item"></shopCard-search>
        </div>
      </div>
      <!--  -->
      <div class="no_res" v-if="res_flag == 2">
         ! 
      </div>
    </div>
    <!--  -->
    <tabbar :i="1" />
  </div>
</template>

<script>
import headerDefault from '../components/header-default'
import shopCardSearch from '../components/shopCard-search'
import tabbar from '../components/tabbar'
export default {
  data () {
    return {
      res_flag: 0, // (1, 2, 0)
      res: [], // ()
      his: [], // ()
      geohash: this.$route.params.geohash,
      keyword: '' // 
    }
  },
  created () {
    var store_his = window.localStorage.getItem('search_his')
    this.his = store_his ? JSON.parse(store_his) : []
  },
  methods:  {
    // 
    showRes (e) {
      e.preventDefault()
      // 
      if (this.keyword === '') return
      // 
      this.getSearchRes()
    },
    // 
    getSearchRes () {
      this.$api.searchRes({
        geohash: this.geohash,
        keyword: this.keyword
      })
      .then(res => {
        if (res.data.status === 0) {
          this.res_flag = 2
          return
        }
        this.res = res.data
        this.isExist()
      })
      .catch(err => {
        // 
        this.isExist()
        this.$api.error(err)
      })
    },
    // 
    isExist () {
      // , 
      this.isStore()
      if (this.res.length == 0) {
        this.res_flag = 2
        return
      }
      this.res_flag = 1
    },
    // 
    isStore () {
      // 
      var store_flag = this.his.some(res => {
        //  true
        return (res === this.keyword)
      })
      // 
      if (!store_flag) {
        this.his.push(this.keyword)
        window.localStorage.setItem('search_his', JSON.stringify(this.his))
      }
    },
    // 
    isHistory () {
      // 0() 0 
      if (this.his.length !== 0 && this.res_flag === 0) {
        return true
      }
    },
    // 
    his_showRes (item) {
      this.keyword = item
      // 
      this.res_flag = -1
      // 
      this.getSearchRes()
    },
    clearAll () {
      this.his = []
      window.localStorage.removeItem('search_his')
    }
  },
  watch: {
    //  keyword ,  0()
    keyword (val) {
      if (val === '') {
        this.res_flag = 0
      }
    }
  },
  components: {
    headerDefault,
    shopCardSearch,
    tabbar
  }
}
</script>

<style lang="scss">
  .searchPage {
    @import '../static/styles/mixin';
    .keyword {
      @include bgc(white);
      padding: 10px;
      .search_input {
        width: 275px;
        height: 36px;
        font-weight: bold;
        @include bgc($pagecl);
        border: 1px solid #e5e5e5;
        padding-left: 5px;
        border-radius: 3px;
      }
      .search_btn {
        @include bgc($maincl);
        margin-left: 5px;
        border: none;
        color: #fff;
        width: 75px;
        height: 36px;
        border-radius: 4px;
      }
    }
    .main {
      .res {
        .shopWord {
          padding: 10px;
          font-size: 14px;
        }
        .shopCard {
          margin-top: 5px;
          @include bgc(white);
          border-bottom: 1px solid $bordercl;
        }
      }
      .no_res {
        @include bgc(white);
        border-top: 3px solid $pagecl;
        font-size: 14px;
        color: #555;
        padding: 15px;
        text-align: center;
      }
      .history {
        .word {
          padding: 15px;
          color: #555;
          font-size: 15px;
          font-weight: bold;
        }
        .content {
          @include bgc(white);
          .search_history_word {
            padding: 15px;
            padding-left: 10px;
            border-bottom: 1px solid $bordercl;
          }
          .clearAll {
            text-align: center;
            font-weight: bold;
            font-size: 16px;
            padding: 15px;
            color: $maincl;
          }
        }
      }
    }
  }
</style>
