import { request } from './request'

export default {
  // 
  error (err) {
    console.log('API , :')
    console.log(err)
  },
  /* Home  */
  // 
  cities (params) {
    return request({
      url: 'v1/cities',
      params
    })
  },
  /* City  */
  // 
  cityInfo (param) {
    return request({
      url: `v1/cities/${param}`
    })
  },
  // 
  searchInfo (params) {
    return request({
      url: 'v1/pois',
      params
    })
  },
  /* Msite  */
  // (, )
  addr (param) {
    return request({
      url: `v2/pois/${param}`
    })
  },
  // 
  foodsCate () {
    return request({
      url: 'v2/index_entry/'
    })
  },
  /* Msite, Food  */
  // 
  shopList (params) {
    return request({
      url: 'shopping/restaurants',
      params
    })
  },
  /* Shop  */
  // 
  shop (param) {
    return request({
      url: `shopping/restaurant/${param}`
    })
  },
  /* Shop -> goods  */
  // 
  goods (params) {
    return request({
      url: 'shopping/v2/menu',
      params
    })
  },
  /* Shop -> comment  */
  // 
  commentTags (param) {
    return request({
      url: `ugc/v2/restaurants/${param}/ratings/tags`
    })
  },
  // 
  comment (params) {
    return request({
      url: `ugc/v2/restaurants/${params.id}/ratings`,
      params: params.params
    })
  },
  /* Search yem */
  // 
  searchRes (params) {
    return request({
      url: 'v4/restaurants',
      params
    })
  },
  /* Service  */
  // FAQ
  explain () {
    return request({
      url: 'v3/profile/explain'
    })
  },
  /* Login  */
  // 
  captchas () {
    return request({
      url: 'v1/captchas'
    })
  },
}
